body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

*:focus {
	outline: none !important;
}

.btn {
	border-radius: 0 !important;
}

.btn:hover, .btn.active:hover, .btn:focus{
	border-color:white;
}

.btn.active {
	background: #ccc;
	border-color: white;
}



