.SettingsPanel {
	position:absolute;
	top:0;
	bottom: 0;
	left: 0;
	right: 0;
}

.SettingsPanel-mode{
	width:100%;
}

.SettingsDock .DockPanel-hide {
	right: 100%;
	top: 20px;
	position:absolute;
}

.SettingsPanel-table {
	width: 100%;
	min-width: 250px;
	table-layout:fixed;
}

.SettingsPanel-table th {
	background: aliceblue;
	-webkit-text-decoration:bold;
	        text-decoration:bold;
	text-align: center;
}

.SettingsPanel-table td div:first-child{
	width: 100%;
}

.SettingsPanel-table td{
	white-space: nowrap;
	overflow: hidden;
	-o-text-overflow:ellipsis;
	   text-overflow:ellipsis;
	text-align: center;
}

.SettingsPanel-table tr input, .SettingsPanel-table tr select{
	background:none;
	height: auto;
	width:100%;
	text-align: center;
}


.SettingsPanel-table tr input[type='checkbox'] {
	width: auto;
}

.SettingsPanel-table .SettingsRow-button {
	width: 70%;
	padding: 0px;
}

.SettingsPanel-table .SettingsRow-action {
	overflow: initial !important;
}

.SettingsPanel-table .SettingsRow-action button{
	vertical-align: top;
	padding: 0px 4px !important;
}

.SettingsPanel-table tr:nth-child(even) {
	background: #f6f6f6;
}

.SettingsPanel-table .separator {
	padding:3px;
	background:aliceblue;
}

.SettingsPanel-tab {
	padding: 5px;
	-webkit-box-shadow:none;
	        box-shadow:none;
	border-radius:0;
	width:100%;
}
th:first-of-type .SettingsPanel-tab{
	border-left: none;
}



