.Filter {
	position: fixed;
	top:0;
	left: 0;
	padding: 0px;
	font-size: 1.5em;
	display: -ms-flexbox;
	display: flex;
	margin:8px;
	-ms-flex-align: center;
	    align-items: center;
	border-radius: 20px;
}

.Filter #searchBar {
	line-height: 15px;
	font-size: 12px;
	height: 20px;
	width: 200px;
	margin-left: 10px;
}

.Filter #filt:hover {
	color: lightgray;
}

.Filter #searchBar:focus {
	outline: none;
	border-bottom: 2px solid blue;
}

.Filter > #searchBar {
	-webkit-box-shadow: none;
	        box-shadow: none;
}



